import react, {useState, useEffect} from "react";
import firebase from "../firebase";
import 'firebase/auth';
const Registr = () => {
    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    let user = firebase.auth().currentUser;
    
    async function regUser(e, email, password){
        e.preventDefault();
        try {
            const data = await firebase.auth().createUserWithEmailAndPassword(email, password);

            user.sendEmailVerification().then( () => {
                console.log("emal sent");
            }).catch(function(error) {
                console.log("bad");
            });

            console.log(data.user.uid, data.user.emailVerified)
        } catch (error) {
            console.log(error.message)
            throw error
        }
    }
    return(
        <form onSubmit={(e) => regUser(e, email, password)}>
            <input type="text" placeholder="Ваш e-mail" value={email} onChange={ (e) => setEmail(e.target.value)}/>
            <input type="password" placeholder="Ваш пароль" value={password} onChange={ (e) => setPassword(e.target.value)}/>
            <button>Зарегестрироваться</button>
        </form>
    )
}

export default Registr;