import react, {useEffect} from "react";
import firebase from "../firebase";
import {connect} from "react-redux";
import {updateMessages} from "../redux/store";
import s from "./Messages.module.css";
import Message from "./Message";
export const db = firebase.firestore();

const Messages = (props) => {

    console.log(props.messages);

    useEffect( () => {
      const unsubscribe = db.collection("messages").orderBy("id", "asc").onSnapshot(
       function(snapshot) {
         let messages = [];
         snapshot.forEach( doc => messages.push({ userId: doc.data().userId, message: doc.data().message, id: doc.data().id}) )
         props.updateMessages( messages )
      });
      return () => unsubscribe();
    }, [])

    return(
       <div className={s.messages}>
           {props.messages.map( (m) => <Message message = {m.message}/> )}
       </div>
    )
}
let mapStateToProps = (state) => {
    return{
      messages: state.messages
    }
}
export default connect(mapStateToProps, {updateMessages})(Messages);